'use client'

import Link from 'next/link'
import { Zap } from 'lucide-react'

export default function PrivacyPage() {
  return (
    <div className="min-h-screen bg-gray-950 text-white">
      {/* Navigation */}
      <nav className="sticky top-0 z-50 border-b border-gray-800 bg-gray-950/80 backdrop-blur-xl">
        <div className="max-w-6xl mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            <Link href="/" className="flex items-center gap-2">
              <div className="w-8 h-8 rounded-lg bg-lime-400 flex items-center justify-center">
                <Zap className="w-5 h-5 text-gray-900" />
              </div>
              <span className="text-lg font-bold text-white">NexFlow</span>
            </Link>
            <Link href="/" className="text-gray-400 hover:text-white">
              ← Back to Home
            </Link>
          </div>
        </div>
      </nav>

      <main className="py-20">
        <div className="max-w-3xl mx-auto px-4">
          <h1 className="text-4xl font-bold mb-2">Privacy Policy</h1>
          <p className="text-gray-400 mb-12">Last updated: December 2024</p>

          <div className="prose prose-invert max-w-none space-y-8">
            <section>
              <h2 className="text-xl font-semibold mb-4">1. Introduction</h2>
              <p className="text-gray-300 leading-relaxed">
                NexFlow ("we", "our", or "us") respects your privacy and is committed to protecting your 
                personal data. This Privacy Policy explains how we collect, use, and safeguard your information 
                when you use our payment infrastructure services.
              </p>
            </section>

            <section>
              <h2 className="text-xl font-semibold mb-4">2. Information We Collect</h2>
              <p className="text-gray-300 leading-relaxed mb-4">
                <strong className="text-white">API Usage Data:</strong> When you integrate with NexFlow, 
                we collect API request logs including endpoints called, timestamps, and response codes.
              </p>
              <p className="text-gray-300 leading-relaxed mb-4">
                <strong className="text-white">Wallet Addresses:</strong> We process wallet addresses 
                provided in payment requests. These are public blockchain addresses.
              </p>
              <p className="text-gray-300 leading-relaxed">
                <strong className="text-white">Transaction Data:</strong> We log transaction metadata 
                for routing optimization and compliance purposes.
              </p>
            </section>

            <section>
              <h2 className="text-xl font-semibold mb-4">3. How We Use Your Information</h2>
              <ul className="list-disc list-inside text-gray-300 space-y-2 ml-4">
                <li>To route payments to optimal providers</li>
                <li>To maintain compliance with regulatory requirements</li>
                <li>To improve our routing algorithms and service reliability</li>
                <li>To provide customer support and troubleshooting</li>
                <li>To detect and prevent fraud or abuse</li>
              </ul>
            </section>

            <section>
              <h2 className="text-xl font-semibold mb-4">4. Data Sharing</h2>
              <p className="text-gray-300 leading-relaxed">
                We share data with payment providers (e.g., Coinbase, x402 facilitators) only as necessary 
                to complete payment transactions. We may also share data with compliance screening providers 
                (e.g., Chainalysis, TRM Labs) for address risk assessment.
              </p>
            </section>

            <section>
              <h2 className="text-xl font-semibold mb-4">5. Data Retention</h2>
              <p className="text-gray-300 leading-relaxed">
                We retain transaction logs and API usage data for up to 7 years to comply with 
                financial record-keeping requirements. You may request deletion of non-essential 
                data by contacting us.
              </p>
            </section>

            <section>
              <h2 className="text-xl font-semibold mb-4">6. Security</h2>
              <p className="text-gray-300 leading-relaxed">
                We implement industry-standard security measures including encryption in transit (TLS), 
                encryption at rest, access controls, and regular security audits. NexFlow is non-custodial 
                and never has access to private keys or signing authority over funds.
              </p>
            </section>

            <section>
              <h2 className="text-xl font-semibold mb-4">7. Your Rights</h2>
              <p className="text-gray-300 leading-relaxed">
                You have the right to access, correct, or delete your personal data. You may also 
                request a copy of your data or object to certain processing activities. Contact us 
                at support@nexflowapp.app to exercise these rights.
              </p>
            </section>

            <section>
              <h2 className="text-xl font-semibold mb-4">8. Contact Us</h2>
              <p className="text-gray-300 leading-relaxed">
                For privacy-related inquiries, contact us at:{' '}
                <a href="mailto:support@nexflowapp.app" className="text-lime-400 hover:underline">
                  support@nexflowapp.app
                </a>
              </p>
            </section>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="border-t border-gray-800 py-8 mt-20">
        <div className="max-w-6xl mx-auto px-4 text-center">
          <p className="text-gray-600 text-sm">© 2025 NexFlow. Smart payment infrastructure.</p>
        </div>
      </footer>
    </div>
  )
}
